import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { isFunction, isEnvNode } from '../common/utils';
import Dialog from '../dialog/dialog';

let that = null;
let container = {};

if (!isEnvNode) {
  container = document.createElement('div');
}

class ConfirmReact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      title: '',
      content: '',
      animation: 'none',
      btnText: ['', ''],
      onOk() {
      },
      onCancel() {
      },
      extraClass: ''
    };
    that = this;
  }

  render() {
    const { show, title, content, animation, onOk, onCancel, btnText, extraClass } = this.state;
    return (
      <Dialog
        show={show}
        title={title}
        onOk={onOk.bind(this)}
        animation={animation}
        okText={btnText[0] != null && btnText[0]}
        cancelText={btnText[1] != null && btnText[1]}
        onCancel={onCancel ? onCancel.bind(this) : false}
        extraClass={extraClass}
      >
        {content}
      </Dialog>
    );
  }
}

if (!isEnvNode) {
  ReactDOM.render(<ConfirmReact />, container);
}

/**
 * @method Confirm
 * @param {Object} option 
 * @param {String | Function} [option.content]  jsx jsx `() => jsx`
 * @param {String} [option.title] 
 * @param {Array} [option.btnText] <3.0.1> /
 * @param {Object} [option.animation] Dialog
 * @param {Boolean} [option.cancel] 
 * @param {String} [option.extraClass] <3.0.15> className
 * @returns {Promise} Promise
 * @description Confirmoption
 *  ``Confirm(content, title, btnText, animation, cancel, extraClass)``
 */
export default function Confirm(
  content = '',
  title = '',
  btnText = ['', ''],
  animation = 'fade',
  cancel = true,
  extraClass = ''
) {
  if (typeof content === 'object') {
    const opt = content;
    title = opt.title != null ? opt.title : '';
    content = opt.content != null ? opt.content : '';
    btnText = opt.btnText != null ? opt.btnText : ['', ''];
    animation = opt.animation != null ? opt.animation : 'fade';
    cancel = opt.cancel != null ? !!opt.cancel : true;
    extraClass = opt.extraClass != null ? opt.extraClass : '';
  }
  content = isFunction(content) ? content() : content;

  return new Promise((resolve) => {
    // duration300
    let duration = animation !== 'none' ? 300 : 0;
    // animation
    if (animation != null && animation.duration != null) {
      ({ duration } = animation);
    }

    function okBtn() {
      setTimeout(() => {
        resolve(true);
      }, duration);
      that.setState({ show: false });
    }

    function cancelBtn() {
      setTimeout(() => {
        resolve(false);
      }, duration);
      that.setState({ show: false });
    }

    that.setState({
      show: true,
      title,
      content,
      btnText,
      animation,
      onOk: okBtn,
      onCancel: cancel ? cancelBtn : false,
      extraClass
    });
  });
}
